.hero__section {
  padding-top: 90px;
  padding-bottom: 156px;
  display: flex;
  justify-content: space-between;
}

.title {
  width: 550px;
  height: 234px;
  font-size: 56px;
  font-weight: 700;
  margin-bottom: 20px;
}

.hero__text {
  width: 537px;
  height: 81px;
  color: #636f87;
  line-height: 150%;
  margin-bottom: 40px;
}
.hero__btn {
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  line-height: 120%;
  font-weight: 700;
  padding: 20px 40px;
  background-color: #1aab7b;
  border-radius: 6px;
  border: none;
}

.card__section {
  background-color: #f2f4f6;
  padding: 50px 0;
}

.card__title {
  text-align: center;
  margin-bottom: 40px;
  color: #101010;
  font-size: 36px;
  font-weight: 600;
  line-height: 140%;
}
.cards {
  display: grid;
  grid: auto / auto auto auto;
  justify-content: space-between;
  gap: 32px;
}

.card {
  border: 2px solid #e1e7ed;
  max-width: 384px;
  border-radius: 6px;
  padding: 40px 30px;
}

.cardone {
  background-color: #1aab7b;
}

.card__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 11px;
}

.card__name {
  font-size: 32px;
  font-weight: 600;
  line-height: 140%;
}

.card__name--one {
  color: #fff;
}

.card__text {
  width: 324px;
  line-height: 150%;
  font-size: 18px;
  margin-bottom: 50px;
}

.card__text--one {
  color: #fff;
}

.card__link {
  font-size: 20px;
  font-weight: 700;
  font-variant: all-small-caps;
  line-height: 140%;
}

.card__link--one {
  color: #fff;
}

.work__section {
  padding: 100px 0;
}

.work__title {
  color: #101010;
  text-align: center;
  font-size: 36px;
  font-weight: 600;
  line-height: 140%;
  margin-bottom: 16px;
}

.work__text {
  max-width: 650px;
  color: #636f87;
  text-align: center;
  font-size: 18px;
  line-height: 150%;
  margin: 0 auto 90px auto;
}

.work__info {
  display: flex;
  gap: 32px;
}

.work__info--one {
  margin-bottom: 100px;
}

.work__info--title {
  font-size: 40px;
  font-weight: 700;
  line-height: 140%;
  max-width: 488px;
  margin-bottom: 20px;
}

.work__info--text {
  color: #636f87;
  max-width: 488px;
  font-size: 18px;
  line-height: 150%;
}

.work__info--two {
  margin-bottom: 100px;
}

.work__img {
  margin-right: 0;
  margin-left: auto;
}
