.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #eeeef4;
}

.header__left {
  display: flex;
  align-items: center;
  gap: 50px;
}

.header__list {
  display: flex;
  gap: 30px;
  list-style-type: none;
}

.header__item {
  cursor: pointer;
  font-size: 16px;
  font-weight: 500;
  line-height: 150%;
  padding-top: 24px;
  padding-bottom: 24px;
}

.header__btn {
  cursor: pointer;
  background-color: #101010;
  border-radius: 6px;
  color: #fff;
  padding: 10px 24px 10px 24px;
}
